.top-bar {
  padding-block: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include on-tablet {
    padding-block: 64px;
  }

  &__icons {
  display: flex;
  gap: 24px;
 }

 &__logo-link {
  display: flex;
 }


 &__logo {
  width: 130px;
  height: 38px;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    width: 187px;
    height: 56px;
   }
 }
}
